  .page-navigation span {
    order: 2;
    width: 100%;
    text-align: center;
    margin: 0.5rem 0;
  }
}

/* PDF Content Styles */
.pdf-content {
  position: relative;
  min-height: 1000px;
  background-color: white;
  padding: 40px;
  margin: 0 auto;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.pdf-content span {
  position: absolute;
  transform-origin: left top;
  white-space: pre;
  line-height: 1;
  margin: 0;
  padding: 0;
}

