  padding: 8px 16px;
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.2s ease;
}

.pdf-editor .export-button:hover:not(:disabled) {
  background-color: #357abd;
}

.pdf-editor .export-button:disabled {
  background-color: #a8c7eb;
  cursor: not-allowed;
}

.pdf-editor .nav-button {
  padding: 8px 16px;
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  font-size: 14px;
  transition: all 0.2s ease;
}

.pdf-editor .nav-button:hover:not(:disabled) {
  background-color: #357abd;
}

.pdf-editor .nav-button:disabled {
  background-color: #a8c7eb;
  color: white;
  cursor: not-allowed;
}

.pdf-editor .page-info {
  color: #000000;
  font-size: 14px;
  padding: 0 10px;
}

/* Bottom navigation buttons */
.bottom-navigation {
  position: fixed;
  bottom: 30px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  padding: 0 50px;
  z-index: 1000;
  pointer-events: none; /* This allows clicking through the container */
}

.bottom-nav-button {
  pointer-events: auto; /* This makes the buttons clickable */
  padding: 10px 20px;
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  font-size: 16px;
  transition: all 0.2s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  opacity: 0.9;
}

.bottom-nav-button:hover:not(:disabled) {
  background-color: #357abd;
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.25);
  opacity: 1;
}

.bottom-nav-button:disabled {
  background-color: #a8c7eb;
  color: white;
  cursor: not-allowed;
  opacity: 0.6;
}

/* Make sure bottom navigation is visible in fullscreen mode */
.tox-fullscreen .bottom-navigation {
  z-index: 10001; /* Higher than TinyMCE fullscreen z-index */
}

/* Ensure the buttons are visible in fullscreen mode */
.tox-fullscreen .editor-container .bottom-navigation {
  display: flex !important;
}

/* OCR Processing styles */
.ocr-progress {
  font-size: 16px;
  margin-bottom: 10px;
  color: #4a90e2;
  font-weight: 500;
}

.progress-bar {
  width: 100%;
  height: 10px;
  background-color: #e1e4e8;
  border-radius: 5px;
  overflow: hidden;
  margin-top: 10px;
}

.progress-fill {
  height: 100%;
  background-color: #4a90e2;
  transition: width 0.3s ease;
}

/* PDF paragraph styles */
.pdf-paragraph {
  margin-bottom: 1em;
  text-align: justify;
}

.pdf-paragraph p {
  margin: 0 0 0.5em 0;
}

/* Image container styles */
.image-container {
  position: relative;
}

.image-container img {
  max-width: 100%;
  height: auto;
}

